import React, { FunctionComponent } from 'react';
import getComponent from '../../../../utils/cms/component-resolver';
import ClassNames from './BlankPage.module.scss';
import AppLink from '../../../../util-components/AppLink/AppLink';

interface Props {
    pageContext: {
        fields: {
            items: any[];
        };
    };
}

const BlankPage: FunctionComponent<Props> = props => {
    const fields = props.pageContext.fields;
    const components = fields.items.map(item => getComponent(item));

    return (
        <div className={ClassNames.BlankPage}>
            <div>
                <AppLink to="/">Home (hard for now)</AppLink>
            </div>
            {components}
        </div>
    );
};

export default BlankPage;
